/*
 * uxAside | v1.5.0
 * Based on uxAside by Marcelo Dolce.
 * http://izimodal.marcelodolce.com
 */
.uxAside {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  transition: margin-top 0.3s ease, height 0.3s ease;
}

.uxAside * {
  -webkit-font-smoothing: antialiased;
}

.uxAside::after {
  content: '';
  width: 100%;
  height: 0;
  opacity: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.35) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#59000000',GradientType=0 );
  transition: height 0.3s ease-in-out, opacity 0.3s ease-in-out;
  pointer-events: none;
}

.uxAside.hasShadow::after {
  height: 30px;
  opacity: 1;
}

.uxAside .uxAside-progressbar {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
}

.uxAside .uxAside-progressbar > div {
  height: 2px;
  width: 100%;
}

.uxAside .uxAside-header {
  background: #88a0b9;
  padding: 14px 18px 15px;
  box-shadow: inset 0 -10px 15px -12px rgba(0, 0, 0, 0.3), 0 0 0 #555;
  overflow: hidden;
  position: relative;
  z-index: 10;
}

.uxAside .uxAside-header-icon {
  font-size: 40px;
  color: rgba(255, 255, 255, 0.5);
  padding: 0 15px 0 0;
  margin: 0;
  float: left;
}

.uxAside .uxAside-header-title {
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.3;
}

.uxAside .uxAside-header-subtitle {
  color: rgba(255, 255, 255, 0.6);
  font-size: 12px;
  line-height: 1.45;
}

.uxAside .uxAside-header-title,
.uxAside .uxAside-header-subtitle {
  display: block;
  margin: 0;
  padding: 0;
  font-family: Arial;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.uxAside .uxAside-header-buttons {
  position: absolute;
  top: 50%;
  right: 10px;
  margin: -17px 0 0;
}

.uxAside .uxAside-header-buttons span {
  float: right;
}

.uxAside .uxAside-header-buttons a {
  display: block;
  float: right;
  z-index: 2;
  outline: none;
  height: 34px;
  width: 34px;
  border: 0;
  padding: 0;
  margin: 0;
  opacity: 0.3;
  border-radius: 50%;
  transition: transform 0.5s cubic-bezier(0.16, 0.81, 0.32, 1), opacity 0.5s ease;
  background-size: 67% !important;
  -webkit-tap-highlight-color: transparent;
  /* For some Androids */
}

.uxAside .uxAside-header-buttons a:hover {
  opacity: 0.8;
}

.uxAside .uxAside-button-close {
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside .uxAside-button-close:hover {
  transform: rotate(180deg);
}

.uxAside .uxAside-button-fullscreen {
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside.isFullscreen .uxAside-button-fullscreen {
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside .uxAside-header.uxAside-noSubtitle {
  height: auto;
  padding: 10px 15px 12px;
}

.uxAside .uxAside-header.uxAside-noSubtitle .uxAside-header-icon {
  font-size: 23px;
  padding-right: 13px;
}

.uxAside .uxAside-header.uxAside-noSubtitle .uxAside-header-title {
  font-size: 15px;
  margin: 3px 0 0;
  font-weight: 400;
}

.uxAside .uxAside-header.uxAside-noSubtitle .uxAside-header-buttons {
  right: 6px;
  margin: -16px 0 0;
}

.uxAside .uxAside-header.uxAside-noSubtitle .uxAside-button {
  height: 30px;
  width: 30px;
}

/* RTL */
.uxAside-rtl {
  direction: rtl;
}

.uxAside-rtl .uxAside-header {
  padding: 14px 18px 15px 40px;
}

.uxAside-rtl .uxAside-header-icon {
  float: right;
  padding: 0 0 0 15px;
}

.uxAside-rtl .uxAside-header-buttons {
  right: initial;
  left: 10px;
}

.uxAside-rtl .uxAside-button {
  float: left;
}

.uxAside-rtl .uxAside-header.uxAside-noSubtitle {
  padding: 10px 15px 12px 40px;
}

.uxAside-rtl .uxAside-header.uxAside-noSubtitle .uxAside-header-icon {
  padding: 0 0 0 13px;
}

/* LIGHT THEME */
.uxAside .uxAside-loader {
  background: #fff url("data:image/svg+xml;base64,") no-repeat 50% 50%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9;
}

.uxAside .uxAside-content-loader {
  background: url("data:image/svg+xml;base64,") no-repeat 50% 50%;
}

.uxAside .uxAside-content {
  zoom: 1;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  /*overflow-y: scroll;*/
}

.uxAside .uxAside-content:before {
  content: '';
  display: table;
}

.uxAside .uxAside-content:after {
  content: '';
  display: table;
  clear: both;
}

.uxAside .uxAside-wrap {
  width: 100%;
  position: relative;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

.uxAside .uxAside-iframe {
  border: 0;
  margin: 0 0 -6px;
  width: 100%;
  transition: height 0.3s ease;
}

.uxAside-overlay {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.uxAside-navigate {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  pointer-events: none;
}

.uxAside-navigate-caption {
  position: absolute;
  left: 10px;
  top: 10px;
  color: #fff;
  line-height: 16px;
  font-size: 9px;
  font-family: arial;
  letter-spacing: 0.1em;
  text-indent: 0;
  text-align: center;
  width: 70px;
  padding: 5px 0;
  text-transform: uppercase;
  display: none;
}

.uxAside-navigate-caption::before, .uxAside-navigate-caption::after {
  position: absolute;
  top: 2px;
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 14px;
  font-size: 12px;
  content: '';
  background-size: 100% !important;
}

.uxAside-navigate-caption:before {
  left: 0;
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside-navigate-caption:after {
  right: 0;
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside-navigate > button {
  position: fixed;
  bottom: 0;
  top: 0;
  border: 0;
  height: 100%;
  width: 84px;
  background-size: 100% !important;
  cursor: pointer;
  padding: 0;
  opacity: 0.2;
  transition: opacity 0.3s ease;
  pointer-events: all;
  margin: 0;
  outline: none;
}

.uxAside-navigate > button:hover {
  opacity: 1;
}

.uxAside-navigate-prev {
  left: 50%;
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside-navigate-next {
  right: 50%;
  background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.uxAside.isAttachedTop {
  margin-top: 0 !important;
  margin-bottom: auto !important;
  border-top-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
}

.uxAside.isAttachedTop .uxAside-header {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.uxAside.isAttachedBottom {
  margin-top: auto !important;
  margin-bottom: 0 !important;
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

.uxAside.isFullscreen {
  max-width: 100% !important;
  max-height: 100% !important;
  border-radius: 0 !important;
}

.uxAside.isAttached {
  border-radius: 0 !important;
}

.uxAside.hasScroll .uxAside-wrap {
  overflow-y: auto;
  overflow-x: hidden;
}

html.uxAside-isOverflow body {
  overflow: hidden;
  position: relative;
}

html.uxAside-isAttached body {
  overflow: hidden;
  position: relative;
}

/* SCROLL */
.uxAside ::-webkit-scrollbar {
  overflow: visible;
  height: 7px;
  width: 7px;
}

.uxAside ::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  background-clip: padding-box;
  border: solid transparent;
  border-width: 0;
  min-height: 28px;
  padding: 100px 0 0;
  box-shadow: inset 1px 1px 0 rgba(0, 0, 0, 0.1), inset 0 -1px 0 rgba(0, 0, 0, 0.07);
}

.uxAside ::-webkit-scrollbar-thumb:active {
  background-color: rgba(0, 0, 0, 0.4);
}

.uxAside ::-webkit-scrollbar-button {
  height: 0;
  width: 0;
}

.uxAside ::-webkit-scrollbar-track {
  background-clip: padding-box;
  border: solid transparent;
  border-width: 0 0 0 2px;
}

.uxAside.transitionIn .uxAside-header {
  animation: uxAside-slideDown 0.7s cubic-bezier(0.7, 0, 0.3, 1);
  animation-delay: 0s;
}

.uxAside.transitionIn .uxAside-header .uxAside-header-icon {
  animation: uxAside-revealIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
}

.uxAside.transitionIn .uxAside-header .uxAside-header-title,
.uxAside.transitionIn .uxAside-header .uxAside-header-subtitle {
  animation: uxAside-slideIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
}

.uxAside.transitionIn .uxAside-header .uxAside-button {
  animation: uxAside-revealIn 1.2s cubic-bezier(0.7, 0, 0.3, 1);
}

.uxAside.transitionIn .uxAside-header .uxAside-header-icon,
.uxAside.transitionIn .uxAside-header .uxAside-header-title {
  animation-delay: 0.4s;
}

.uxAside.transitionIn .uxAside-header .uxAside-header-subtitle {
  animation-delay: 0.5s;
}

.uxAside.transitionIn .uxAside-iframe,
.uxAside.transitionIn .uxAside-wrap {
  animation: uxAside-fadeIn 1.3s;
}

.uxAside.transitionOut .uxAside-header {
  transition: none !important;
}

.uxAside.transitionOut .uxAside-header * {
  transition: none !important;
}

.uxAside.fadeOut {
  animation: uxAside-fadeOut 0.5s;
  animation-fill-mode: forwards;
}

/* MODAL ANIMATIONS */
/* ANIMATIONS */
.uxAside-overlay.fadeOut,
.uxAside-navigate.fadeOut {
  animation: uxAside-fadeOut 0.5s;
  animation-fill-mode: forwards;
}

.uxAside .fadeOut {
  animation: uxAside-fadeOut 0.5s;
  animation-fill-mode: forwards;
}

.uxAside.fadeIn {
  animation: uxAside-fadeIn 0.5s;
}

.uxAside-overlay.fadeIn,
.uxAside-navigate.fadeIn {
  animation: uxAside-fadeIn 0.5s;
}

.uxAside .fadeIn {
  animation: uxAside-fadeIn 0.5s;
}

.uxAside.comingIn {
  animation: uxAside-comingIn 0.5s ease;
}

.uxAside-overlay.comingIn {
  animation: uxAside-comingIn 0.5s ease;
}

.uxAside.comingOut,
.uxAside-overlay.comingOut {
  animation: uxAside-comingOut 0.5s cubic-bezier(0.16, 0.81, 0.32, 1);
  animation-fill-mode: forwards;
}

.uxAside.bounceInDown,
.uxAside-overlay.bounceInDown {
  animation: uxAside-bounceInDown 0.7s ease;
}

.uxAside.bounceOutDown,
.uxAside-overlay.bounceOutDown {
  animation: uxAside-bounceOutDown 0.7s ease;
}

.uxAside.bounceInUp,
.uxAside-overlay.bounceInUp {
  animation: uxAside-bounceInUp 0.7s ease;
}

.uxAside.bounceOutUp,
.uxAside-overlay.bounceOutUp {
  animation: uxAside-bounceOutUp 0.7s ease;
}

.uxAside.fadeInDown,
.uxAside-overlay.fadeInDown {
  animation: uxAside-fadeInDown 0.7s cubic-bezier(0.16, 0.81, 0.32, 1);
}

.uxAside.fadeOutDown,
.uxAside-overlay.fadeOutDown {
  animation: uxAside-fadeOutDown 0.5s ease;
}

.uxAside.fadeInUp,
.uxAside-overlay.fadeInUp {
  animation: uxAside-fadeInUp 0.7s cubic-bezier(0.16, 0.81, 0.32, 1);
}

.uxAside.fadeOutUp,
.uxAside-overlay.fadeOutUp {
  animation: uxAside-fadeOutUp 0.5s ease;
}

.uxAside.fadeInLeft,
.uxAside-overlay.fadeInLeft {
  animation: uxAside-fadeInLeft 0.7s cubic-bezier(0.16, 0.81, 0.32, 1);
}

.uxAside.fadeOutLeft,
.uxAside-overlay.fadeOutLeft {
  animation: uxAside-fadeOutLeft 0.5s ease;
}

.uxAside.fadeInRight,
.uxAside-overlay.fadeInRight {
  animation: uxAside-fadeInRight 0.7s cubic-bezier(0.16, 0.81, 0.32, 1);
}

.uxAside.fadeOutRight,
.uxAside-overlay.fadeOutRight {
  animation: uxAside-fadeOutRight 0.5s ease;
}

.uxAside.flipInX,
.uxAside-overlay.flipInX {
  animation: uxAside-flipInX 0.7s ease;
}

.uxAside.flipOutX,
.uxAside-overlay.flipOutX {
  animation: uxAside-flipOutX 0.7s ease;
}

@keyframes uxAside-comingIn {
  0% {
    opacity: 0;
    /*transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );*/
    transform: scale(0.9) translateY(-20px) perspective(600px) rotateX(10deg);
  }
  100% {
    opacity: 1;
    /*transform: scale(1) translateY(0) perspective( 600px ) rotateX( 0 ); */
    transform: scale(1) translateY(0) perspective(600px) rotateX(0);
  }
}

@keyframes uxAside-comingOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.9);
  }
}

@keyframes uxAside-fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes uxAside-fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes uxAside-slideIn {
  0% {
    opacity: 0;
    transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes uxAside-slideDown {
  0% {
    opacity: 0;
    transform: scale(1, 0) translateY(-40px);
    transform-origin: center top;
  }
}

@keyframes uxAside-revealIn {
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 1);
  }
}

@keyframes uxAside-bounceInDown {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  0% {
    opacity: 0;
    transform: translate3d(0, -1000px, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(0, 25px, 0);
  }
  75% {
    transform: translate3d(0, -10px, 0);
  }
  90% {
    transform: translate3d(0, 5px, 0);
  }
  to {
    transform: none;
  }
}

@keyframes uxAside-bounceOutDown {
  20% {
    transform: translate3d(0, 10px, 0);
  }
  40%, 45% {
    opacity: 1;
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(0, 1000px, 0);
  }
}

@keyframes uxAside-bounceInUp {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  from {
    opacity: 0;
    transform: translate3d(0, 1000px, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(0, -20px, 0);
  }
  75% {
    transform: translate3d(0, 10px, 0);
  }
  90% {
    transform: translate3d(0, -5px, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes uxAside-bounceOutUp {
  20% {
    transform: translate3d(0, -10px, 0);
  }
  40%, 45% {
    opacity: 1;
    transform: translate3d(0, 20px, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(0, -1000px, 0);
  }
}

@keyframes uxAside-fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100px, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes uxAside-fadeOutDown {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, 100px, 0);
  }
}

@keyframes uxAside-fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100px, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes uxAside-fadeOutUp {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, -100px, 0);
  }
}

@keyframes uxAside-fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-200px, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes uxAside-fadeOutLeft {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(-200px, 0, 0);
  }
}

@keyframes uxAside-fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(200px, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes uxAside-fadeOutRight {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(200px, 0, 0);
  }
}

@keyframes uxAside-flipInX {
  0% {
    transform: perspective(400px) rotateX(60deg);
    opacity: 0;
  }
  40% {
    transform: perspective(400px) rotateX(-10deg);
  }
  70% {
    transform: perspective(400px) rotateX(10deg);
  }
  100% {
    transform: perspective(400px) rotateX(0deg);
    opacity: 1;
  }
}

@keyframes uxAside-flipOutX {
  from {
    transform: perspective(400px);
  }
  30% {
    transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
    opacity: 1;
  }
  to {
    transform: perspective(400px) rotate3d(1, 0, 0, 40deg);
    opacity: 0;
  }
}
